<template>

  <div class="login">
    <div class="img">
      <img src="../../picture/Welcome.png">
    </div>
    <div id="board">
      <Form ref="formInline" :model="formInline" :rules="ruleInline" >
        <FormItem prop="user">
          <p><span class="title1">标准化村(社区)</span><br><span class="title2">台账清单</span></p><br>
          <Input type="text" v-model="formInline.user" placeholder="请输入用户名">
          </Input>
        </FormItem>
        <FormItem prop="password">
          <Input type="password" v-model="formInline.pwd" placeholder="请输入用户密码">
          </Input>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleSubmit">登录</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Login',
    data () {
      return {
        formInline: {
          user: '',
          pwd: ''
        },
        ruleInline: {
          user: [
            { required: true, message: '请输入用户名', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入用户密码', trigger: 'blur' },
            { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
          ]
        }
      }
    },
    methods:{
      handleSubmit(name) {
        // this.$refs[name].validate((valid) => {
        //   if (valid) {
        //     this.$Message.success('Success!');
        //   } else {
        //     this.$Message.error('Fail!');
        //   }
        // })
        this.$router.push({name:'Index'});
      },
    },
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login{
    height:100vh;
    min-width:1024px;
    min-height:768px;
  }
  img{
    width:100%;
    height:100%;
  }
  .img{
    position:absolute;
    z-index:-1;
    width:100%;
    height:100%;
    min-width:1024px;
    min-height: 768px;
  }
  #board{
    color: #8C98CC;
    font-size: 16px;
    text-align: center;
    display: flex;
    justify-content: center;
    background-color: rgba(255,255,255,0);
    position:relative;
    top:30%;
    left: 57%;
    height:360px;
    width:275px;
    padding:40px 0;
  }
  .title1{
    font-size: 20px;
    font-family: MicrosoftYaHeiLight;

  }
  .title2{
    font-size: 26px;
    font-family: MicrosoftYaHeiLight ;
  }
  button{
    color: #F7F7F7;
    font-size: 18px;
    background-color: #8C98CC;
    border-radius: 50px;
    display: inline-block;
    height: 32px;
    width: 210px;
    line-height:0px;
    border:0;
  }
</style>
<style>
  #board .ivu-input{
    border: 0;
    text-align: center;
    border-bottom:2px solid #eee;
    outline: none;
    background-color: rgba(255,255,255,0);
  }

</style>



