<template>
  <div class="pPad1 pad">
    <div class="card"></div>
  </div>
</template>

<script>
  export default {
    name: 'pPad1',
    data () {
      return {
        form1: {
          input1: '',
          input2: '',
          input3: ''
        }
      }
    },
    methods:{
      changeView:function(){
        this.$emit('changeSystemView','Pad1');
      }
    }
  }
</script>


<style scoped>
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:10px;
    height:450px;

  }
  .pad1 .ivu-input-wrapper{
    width: 300px;

  }

</style>
<style>
  .pad{
    background:rgb(245, 247, 249);
    width: 100%;
  }
</style>



