<template>
  <div class="pad1 pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>制度机制</BreadcrumbItem>
        <BreadcrumbItem>组织架构</BreadcrumbItem>
        <BreadcrumbItem>组织架构和责任人基本信息</BreadcrumbItem>
      </Breadcrumb>
      <h1>组织架构和责任人基本信息</h1><br>
    </div>

    <div class="form card">
      <Form :model="form1" label-position="right" :label-width="100" >
        <FormItem label="责任人姓名">
          <Input v-model="form1.input1"></Input>
        </FormItem>
        <FormItem label="联系电话">
          <Input v-model="form1.input2"></Input>
        </FormItem>
        <FormItem label="职务">
          <Input v-model="form1.input3"></Input>
        </FormItem>
        <FormItem>
          <Button type="primary" :style="{width:'70%'}" @click.native="handleSubmit">提交</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Pad4',
    data () {
      return {
        form1: {
          input1: '',
          input2: '',
          input3: ''
        }
      }
    },
    methods:{
      handleSubmit:function(){
        this.$emit('result','Success1');
      }
    }
  }
</script>


<style scoped>
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:10px;
    height:450px;

  }

</style>
<style>

  .pad1 .ivu-input-wrapper{
    width: 300px;
  }

</style>



