<template>
  <div class="topHead">
    <div class="layout">
      <Header :style="{background: '#fff'}">
        <Menu mode="horizontal"  active-name="1">
          <div class="layout-back" @click="changeView">
            <Icon type="ios-arrow-back" />{{currentView}} - {{pad}}
          </div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-search"></Icon>
            </MenuItem>
            <MenuItem name="2">
              <Icon type="md-person" />admin
            </MenuItem>
            <MenuItem name="3" @click.native="exit">
              <Icon type="md-arrow-round-forward" />退出
            </MenuItem>
          </div>
        </Menu>
      </Header>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'topHead',
    data () {
      return {
        arr:[this.pad,'newAlbum','uploadPicture','Pad1','Upload1','Upload2','uploadPicture','Upload2','hisPad5'],
      }
    },
    props:['pad','currentView'],
    methods:{
      changeView(){
        //二级页返回跳转 ，判断是否是二级页
        if(this.arr.toString().indexOf(this.currentView)>=0) {
          switch(this.pad) {
          //基本概况页跳转
            case 'msg':
              this.$emit('changeView', 'Message');
              break;
            //制度机制页跳转
            //副栏第一项点击返回进行一级页跳转，为路由跳转
            case 'Pad1':
              this.$router.push({path: '/index'});
              break;
            case 'Pad2':
            case 'Pad3' :
            case 'Pad4':
            case 'Pad5':
            case 'Pad6':
            case 'Pad7':
              this.$emit('changeSystemView', 'Pad' + (this.pad.substr(3, 1) - 1));//跳转到上一个二级页
              break;
          //防灾准备页跳转
            case 'pPad1':
              this.$router.push({path: '/system'});//一级页跳转
              break;
            case 'pPad2':
              this.$emit('changePrepView', 'Pad1', 'pPad1');
              break;
            case 'pPad3':
              this.$emit('changePrepView', 'newAlbum', 'pPad2');
              break;
            case 'pPad4':
              this.$emit('changePrepView', 'uploadPicture', 'pPad3');
              break;
            case 'pPad5':
              this.$emit('changePrepView', 'newAlbum', 'pPad4');
              break;
          //防灾设施页跳转
            case 'fPad1':
              this.$router.push({path: '/prepare'});//一级页跳转
              break;
            case 'fPad2':
              this.$emit('changeView', 'newAlbum', 'fPad1');
              break;
            case 'fPad3':
              this.$emit('changeView', 'newAlbum', 'fPad2');
              break;
            case 'fPad4':
              this.$emit('changeView', 'Upload1', 'fPad3');
              break;
            case 'fPad5':
              this.$emit('changeView', 'Upload1', 'fPad4');
              break;
            case 'fPad6':
              this.$emit('changeView', 'uploadPicture', 'fPad5');
              console.log('c');
              break;
            //历史实践页跳转
            case 'hPad1':
              this.$router.push({path: '/facility'});//一级页跳转
              break;
            case 'hPad2':
              this.$emit('changeView', 'Upload2', 'hPad1');
              break;
            case 'hPad3':
              this.$emit('changeView', 'Upload1', 'hPad2');
              break;
            case 'hPad4':
              this.$emit('changeView', 'Upload2', 'hPad3');
              break;
            case 'hPad5':
              this.$emit('changeView', 'Upload1', 'hPad4');
              break;
          }
        }
        //三级子页返回跳转
        else{
          switch(this.currentView){
            default:
              this.$emit('changeSystemView',this.pad);
              break;
            case 'addAlbum':
            case 'showPic':
              this.$emit('changePrepView','newAlbum',this.pad);
              if(this.pad=='fPad3'||this.pad=='fPad4'||this.pad=='fPad6'||this.pad=='hPad2'||this.pad=='hPad3'||this.pad=='hPad4')
                this.$emit('changeView','Success2',this.pad);
              else if(this.pad=='fPad5')
                this.$emit('changeView','fPad5Album',this.pad);
              else if(this.pad=='hPad1')
                this.$emit('changeView','Upload2',this.pad);
              else if(this.pad=='hPad5')
                this.$emit('changeView','hPad5Album',this.pad);
              else
                this.$emit('changeView','newAlbum',this.pad);
              break;
            case 'Success2':
              if(this.pad=='fPad6'||this.pad=='hPad1')
                this.$emit('changeView','Upload2',this.pad);
              else {
                this.$emit('changePrepView', 'uploadPicture', this.pad);
                this.$emit('changeView', 'upload1', this.pad);
              }
              break;
            case 'fPad5Album':
              this.$emit('changeView','uploadPicture',this.pad);
              break;
            case 'hPad5Album':
              this.$emit('changeView','hisPad5',this.pad);
              break;
          }
        }
      },
      exit:function() {
        this.$bus.$emit('changeFlag'); //使侧边栏消失
        this.$router.push({path: '/'});
      }
    }
  }
</script>


<style>
  .topHead{
    border:0;
    margin:0 5px;
    height:99px;
  }
  .topHead h1{
    font-size: 20px;
    line-height: 40px;
  }
  .topHead .ivu-menu-horizontal{
    height:99px;
    padding:20px;
  }
  .topHead .layout-back{
    float: left;
  }
  .topHead .layout-nav{
    /*margin-right: 20px;*/
    float:right;
  }
  .topHead .ivu-breadcrumb > span:last-child {
    color:#C1C1C1;
  }
  .topHead .ivu-menu-light.ivu-menu-horizontal .ivu-menu-item{
    border:0;
  }
  .topHead .ivu-menu-horizontal.ivu-menu-light::after {
    height:0;
  }
  .topHead .ivu-menu .ivu-icon{
    font-size: 20px;
    opacity:0.5;
  }
  .topHead .ivu-layout-topHead{
    /*padding:0 5px;*/
  }
  .topHead .ivu-layout-header{
    padding: 0;
  }

</style>



