<template>
  <div class="Upload2 pad">

    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>{{title1}}</BreadcrumbItem>
        <BreadcrumbItem>{{title2}}</BreadcrumbItem>
        Upload2-{{pad}}
      </Breadcrumb>
      <h1>{{title2}}</h1><br>
      <span>{{msg}}</span>
    </div>

    <div class="card center form">
      <Form :model="form6" >
        <FormItem>
          <div class="add">
            添加照片：
            <Upload action="//jsonplaceholder.typicode.com/posts/">
              <Button icon="ios-cloud-upload-outline">Upload files</Button>
            </Upload>
          </div>
        </FormItem>
        <FormItem>
          <Button type="primary" :style="{width:'60%'}" @click="changeView">提交</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Upload2',
    data () {
      return {
        form6: {
          input1: ''
        },
        title1:'',
        title2:'',
        msg:''
      }
    },
    props:['pad'],
    methods:{
      changeView:function(){
        this.$emit('result','Success2');
        console.log(this.pad);
      }
    },
    mounted:function(){
      switch(this.pad){
        case 'fPad6':
          this.title1='防灾设施';
          this.title2='科普宣传';
          this.msg='要求上传科普长廊、橱窗或农村大礼堂等公共场所宣传设施的照片';
          break;
        case 'hPad1':
          this.title1='历史实践';
          this.title2='信息接收';
          this.msg='接收气象预警的图片（包括但不限于显示屏、手机等移动智能终端截屏）';
          break;
        case 'hPad3':
          this.title1='历史实践';
          this.title2='信息传播';
          this.msg='转发、传播预警信息的图片（包括但不限于（包括但不限于显示屏、手机等移动智能终端截屏）';
          break;
      }
    }
  }
</script>


<style scoped>
  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .center{
    text-align: center;
  }
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:30px;
  }
</style>
<style>
  .Upload .ivu-form{
    width:500px;
  }
  .Upload .ivu-form-item-content{
    margin:0;
  }
</style>



