<template>
  <div id="app">
      <side-menu v-show="flag"></side-menu>
      <router-view @changeFlag="changeFlag" :style="{width:'100%'}"/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      flag:false
    }
  },
  methods:{
    changeFlag(flag){
      this.flag=flag;
    },
  },
  mounted(){
    let _this=this;
    _this.$bus.$off("changeFlag");
    _this.$bus.$on('changeFlag',function(){
      _this.flag=false;
      console.log(this.flag)
    })
  }
}
</script>

<style>
#app {
  display: flex;
  height:100vh;
}
.ivu-breadcrumb > span:last-child {
  /*标题栏最后一个面包导航设置为灰色*/
  color:#C1C1C1;
}
.card{
  background: white;
  padding:20px;
  margin:10px;
}
</style>



