<template>
  <div class="pPad3 pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>{{title1}}</BreadcrumbItem>
        <BreadcrumbItem>{{title2}}</BreadcrumbItem>
        uploadPicture-{{pad}}
      </Breadcrumb>
      <h1>{{title2}}</h1><br>
      <span>{{msg}}</span>
    </div>

    <div class="card center">
      <div class="upload">
        <div class="add">
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button icon="ios-cloud-upload-outline">Upload files</Button>
          </Upload>
          <p>{{msg1}}</p>
        </div>
        <div class="add">
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button icon="ios-cloud-upload-outline">Upload files</Button>
          </Upload>
          <p>{{msg2}}</p>
        </div>
      </div>
      <Button type="primary" :style="{width:'40%'}" @click="handleSubmit">提交</Button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'pPad3',
    data () {
      return {
        title1:'',
        title2:'',
        msg:'',
        msg1:'',
        msg2:'',
      }
    },
    props:['pad'],
    methods: {
      handleSubmit: function () {
        switch (this.pad) {
          default:
            this.$emit('result', 'Success2');
            break;
          case 'fPad5':
            this.$emit('result', 'fPad5Album');
            break;
        }
      },
    },
      mounted() {
        switch (this.pad) {
          default:
            this.title1 = '防灾准备';
            this.title2 = '应急认证';
            this.msg = '两种扫描件任选一种上传即可';
            this.msg1 = '应急准备认证表（含认证意见）扫描件';
            this.msg2 = '通过应急准备的文件扫描件';
            break;
          case 'fPad5':
            this.title1 = '防灾设施';
            this.title2 = '防雷设施';
            this.msg = '两种照片都要上传';
            this.msg1 = '防雷安全警示牌照片';
            this.msg2 = '建筑物防雷设施照片（包括住房和学校、医院、凉亭等公共设施）';
            break;
          case 'pPad2':
            this.title1 = '防灾准备';
            this.title2 = '应急演练';
            this.msg = '两种照片都要上传';
            this.msg1 = '防雷安全警示牌照片';
            this.msg2 = '建筑物防雷设施照片（包括住房和学校、医院、凉亭等公共设施）';
            break;
        }
      }
  }
</script>


<style scoped>
  .card{
    background: white;
    padding:40px;
    margin:10px;
  }
  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .center{
    text-align: center;
  }
  .upload{
    display: flex;
    margin:auto;
  }
  .add{
    width:340px;
    height:300px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }
</style>
<style>
</style>



