<template>
  <div class="pad1 pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>制度机制</BreadcrumbItem>
        <BreadcrumbItem>工作制度</BreadcrumbItem>
        <BreadcrumbItem>日常工作制度</BreadcrumbItem>
      </Breadcrumb>
      <h1>日常工作制度</h1><br>
    </div>

    <div class="card">
      <Form :model="form3" label-position="top">
        <br><br><h2>工作制度</h2><br><br>
        <FormItem label="正文">
          <Input v-model="form3.input1" type="textarea" :rows="15" :style="{width:'100%'}"></Input>
        </FormItem>
        <FormItem :style="{textAlign:'center'}">
          <Button type="primary" @click.native="handleSubmit" :style="{width:'20%'}">提交</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Pad1',
    data () {
      return {
        form3: {
          input1: '',
          input2: '',
        }
      }
    },
    methods:{
      handleSubmit:function(){
        this.$emit('result','Success1');
      }
    }
  }
</script>


<style scoped>
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:10px;
    height:450px;

  }
  .pad1 .ivu-input-wrapper{
    width: 300px;

  }
  .pad{
    background:rgb(245, 247, 249);
    width: 100%;
  }
</style>
<style>

</style>



