<template>
  <div class="system grey">
    <div class="small-menu">
    <Sider :style="{ position: 'fixed',height: '100vh', left: '210px',color:'white',minWidth:'107px',maxWidth:'107px',flexBasis: '107px'}">
        <div class="title">
          <div class="title-name">应急计划</div>
        </div>
        <Menu :active-name="pad" theme="light" width="107px" >
          <MenuItem name="Pad1" @click.native="changeSystemView('Pad1')">
            <span>组织架构</span>
          </MenuItem>
          <MenuItem name="Pad2" @click.native="changeSystemView('Pad2')">
            <span>气象信息员</span>
          </MenuItem>
          <MenuItem name="Pad3" @click.native="changeSystemView('Pad3')">
            <span>工作制度</span>
          </MenuItem>
          <MenuItem name="Pad4" @click.native="changeSystemView('Pad4')">
            <span>应急处置表</span>
          </MenuItem>
          <MenuItem name="Pad5" @click.native="changeSystemView('Pad5')">
            <span>通讯列表</span>
          </MenuItem>
          <MenuItem name="Pad6" @click.native="changeSystemView('Pad6')">
            <span>重点单位</span>
          </MenuItem>
          <MenuItem name="Pad7" @click.native="changeSystemView('Pad7')">
            <span>网格员</span>
          </MenuItem>
        </Menu>
      </Sider>
    </div>
    <div class="main">
      <top-head  :pad="pad" :currentView="systemView" @changeSystemView="changeSystemView"></top-head>
      <div class="systemViews">
        <component :is="systemView" @changeSystemView="changeSystemView" :pad="pad" @result="handleResult"></component>
      </div>
    </div>
  </div>
</template>

<script>
  import Pad1 from './views/Pad1'
  import Pad2 from './views/Pad2'
  import Pad3 from './views/Pad3'
  import Pad4 from './views/Pad4'
  import Pad5 from './views/Pad5'
  import Pad6 from './views/Pad6'
  import Pad7 from './views/Pad7'
  export default {
    name: 'System',
    data () {
      return {
        systemView:'Pad1',
        pad:'Pad1',
      }
    },
    methods:{
      changeSystemView(data){
        this.systemView = data;
        this.pad = data;
      },
      handleResult(data){
        this.systemView = data;
        console.log(data);
      }
    },
    components:{
      Pad1,Pad2,Pad3,Pad4,Pad5,Pad6,Pad7
    },
    created(){
      this.$emit('changeFlag',true);
      this.$bus.$emit('changePad','System');
    }
  }
</script>


<style scoped>
.system{
  display: flex;
}
</style>
<style>
  .main{
    width:100%;
  }
  .small-menu{
    min-width:107px;
    height:100vh;
  }
  .title-name{
    /*控制菜单栏的标题*/
    font-size: 14px;
    color: #CACACA ;
    height:99px;
    display: flex;
    align-items:center;
    justify-content:center;
  }
  .system .ivu-layout-sider-children{
    /*控制小菜单栏背景颜色*/
    background: white;
    width:107px;
  }
  .system .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title {
    /*控制菜单栏里的文字不换行*/
    padding:14px 16px;
  }
  .ivu-menu-item{
    /*控制菜单栏大小*/
    height:72px;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu)::after {
    /*去掉选择条*/
    width:0;
  }
  .ivu-menu-vertical.ivu-menu-light::after {
    /*去掉选择条*/
    width:0;
  }
</style>
<link rel="stylesheet" type="text/css" href="./assets/index.css">



