// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import TopHead from './components/topHead'
import SideMenu from './components/Sider'
import Success1 from './components/common/Success1'
import Success2 from './components/common/Success2'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import bus from 'vue-bus'

Vue.use(bus)
Vue.config.productionTip = false
Vue.use(iView)
/* eslint-disable no-new */


Vue.component('TopHead',TopHead)
Vue.component('Success1',Success1)
Vue.component('Success2',Success2)
Vue.component("side-menu",SideMenu)


new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



