<template>
  <div class="pad4 pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>制度机制</BreadcrumbItem>
        <BreadcrumbItem>应急处置表</BreadcrumbItem>
      </Breadcrumb>
      <h1>气象灾害预警信号为先导的分灾种气象灾害应急处置表</h1><br>
    </div>

    <div class="form card">
      <Form :model="form4" label-position="right" :label-width="100" >
        <FormItem label="灾种：" >
          <Input v-model="form4.input1"></Input>
        </FormItem>
        <FormItem label="预警等级：">
          <Input v-model="form4.input2"></Input>
        </FormItem>
        <FormItem label="处置人员：">
          <Input v-model="form4.input3"></Input>
        </FormItem>
        <FormItem label="行动内容：">
          <Input type="textarea" :rows="6" v-model="form4.input4"></Input>
        </FormItem>
        <FormItem>
          <Button type="primary" :style="{width:'70%'}" @click.native="handleSubmit">提交</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Pad1',
    data () {
      return {
        form4: {
          input1: '',
          input2: '',
          input3: '',
          input4: ''
        }
      }
    },
    methods:{
      handleSubmit:function(){
        this.$emit('result','Success1');
      }
    }
  }
</script>


<style scoped>
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:30px;
  }
  .pad4 .ivu-input-wrapper{
    width: 400px;
  }
  .pad{
    background:rgb(245, 247, 249);
    width: 100%;
  }
</style>



