<template>
  <div class="Upload1 pad">

    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>{{title1}}</BreadcrumbItem>
        <BreadcrumbItem>{{title2}}</BreadcrumbItem>Upload1 - {{pad}}
      </Breadcrumb>
      <h1>{{title2}}</h1><br>
      <span>{{msg}}</span>
    </div>

    <div class="card center form">
      <Form :model="form3"  :label-width="100" >
        <FormItem label="图片说明：" >
          <Input type="textarea" :rows="6" v-model="form3.input4"></Input>
        </FormItem>
        <FormItem>
          <div class="add">
            添加照片：
            <Upload action="//jsonplaceholder.typicode.com/posts/">
              <Button icon="ios-cloud-upload-outline">Upload files</Button>
            </Upload>
          </div>
        </FormItem>
        <FormItem>
          <Button type="primary" :style="{width:'60%'}" @click="changeFclView">提交</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Upload1',
    data () {
      return {
        title1:'',
        title2:'',
        msg:'',
        form3: {
          input1: '',
          input2: '',
        }
      }
    },
    props:['pad'],
    methods: {
      changeFclView: function () {
        this.$emit('result', 'Success2');
      },
      showMsg: function () {
        switch (this.pad) {
          default:
            this.title1 = '防灾设施';
            this.title2 = '接收传播设施';
            this.msg = '要求上传的图片为大喇叭或显示屏等固定接收传播设施的照片';
            break;
          case 'fPad4':
            this.title1 = '防灾设施';
            this.title2 = '避灾场所';
            this.msg = '要求上传的图片为避灾场所、警示牌和避灾路线图实景照片';
            break;
          case 'hPad2':
            this.title1 = '历史实践';
            this.title2 = '组织部署';
            this.msg = '要求上传的图片为相关责任人组织部署的图片（包括但不限于文件、会议、现场防御组织）';
            break;
          case 'hPad4':
            this.title1 = '历史实践';
            this.title2 = '现场防御';
            this.msg = '开展现场防御的图片（包括但不限于入户通知、组织人员安置转移、发放避灾物资等）';
            break;
        }
      },
    },
    mounted(){
      this.showMsg();
    },
    updated(){
      this.showMsg();
    }
  }
</script>


<style scoped>
  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .center{
    text-align: center;
  }
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:30px;
  }
</style>
<style>
  .Upload1 .ivu-form{
    width:500px;
  }
</style>



