<template>
  <div class="showAlbum pad">
    <div class="card">
      <h1>{{padName}}照片组</h1>showPicture-{{pad}}
      <span class="right" @click="changeView">去修改</span>
    </div>
    <div class="card">
      显示上传的图片
    </div>
  </div>
</template>

<script>
  export default {
    name: 'showAlbum',
    data () {
      return {
        List: [
          {
            id:1,
            name: 'a42bdcc1178e62b4694c830f028db5c0',
            url: 'https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar',
            status:1
          },
        ],
        padName:''
      }
    },
    props:['pad'],
    methods:{
      changeView:function(){
        switch(this.pad) {
          case 'fPad3':
            this.$emit('result', 'upload1');
            break;
          case 'pPad2':
          case 'pPad4':
          case 'pPad5':
            this.$emit('result', 'addAlbum',this.pad);
            break;
          case 'pPad3':
            this.$emit('result', 'uploadPicture');
            break;
          case 'fPad4':
            this.$emit('result', 'Upload1');
            break;
          case 'fPad6':
            this.$emit('result', 'Upload2');
            break;
          case 'hPad1':
            this.$emit('result', 'Upload2');
            break;
          case 'hPad2':
            this.$emit('result', 'Upload1');
            break;
          case 'hPad3':
            this.$emit('result', 'Upload2');
            break;
          case 'hPad4':
            this.$emit('result', 'Upload1');
            break;
          case 'hPad5':
            this.$emit('result', 'hisPad5');
            break;
          default:
            this.$emit('result', 'uploadPicture');
            break;
        }
      },
    },
    mounted: function(){
      switch(this.pad){
        case 'pPad3':
          this.padName='应急认证';
          break;
        case 'pPad4':
          this.padName='科普培训';
          break;
        case 'pPad5':
          this.padName='科普活动';
          break;
        case 'fPad1':
          this.padName='气象服务站';
          break;
        case 'fPad2':
          this.padName='监测设施';
          break;
        case 'fPad3':
          this.padName='接收传播设施';
          console.log('s');
          break;
        case 'fPad4':
          this.padName='避灾场所';
          break;
        case 'fPad5':
          this.padName='防雷设施';
          break;
        case 'hPad1':
          this.padName='信息接收';
          break;
        case 'hPad2':
          this.padName='组织部署';
          break;
        case 'hPad3':
          this.padName='信息传播';
          break;
        case 'hPad5':
          this.padName='灾情调查';
          break;
        default:
          this.padName='应急演练';
          break;

      }
    }
  }
</script>


<style scoped>
  .showAlbum .ivu-form{
    width:500px;
  }
  .showAlbum .ivu-select{
    width:30%;
  }
  .card{
    position: relative;
    display: flex;
  }
  .right{
    display:block;
    position:absolute;
    right:0;
  }
</style>



