<template>
  <div class="history grey">
    <div class="small-menu">
      <Sider :style="{ position: 'fixed',height: '100vh', left: '210px',color:'white',minWidth:'107px',maxWidth:'107px',flexBasis: '107px'}">
        <div class="title">
          <div class="title-name">当年灾害</div>
        </div>
        <Menu :active-name="pad" theme="light" width="107px" >
          <MenuItem name=hPad1 @click.native="changeHstView('Upload2','hPad1')">
            <span>信息接收</span>
          </MenuItem>
          <MenuItem name="hPad2" @click.native="changeHstView('Upload1','hPad2')">
            <span>组织部署</span>
          </MenuItem>
          <MenuItem name="hPad3" @click.native="changeHstView('Upload2','hPad3')">
            <span>信息传播</span>
          </MenuItem>
          <MenuItem name="hPad4" @click.native="changeHstView('Upload1','hPad4')">
            <span>现场防御</span>
          </MenuItem>
          <MenuItem name="hPad5" @click.native="changeHstView('hisPad5','hPad5')">
            <span>灾情调查</span>
          </MenuItem>
        </Menu>
      </Sider>
    </div>
    <div class="main">
      <top-head :pad="pad" :current-view="historyView" @changeView="changeHstView"></top-head>
      <div class="historyViews">
        <component :is="historyView" @changeHstView="changeHstView" :pad="pad" @result="handleResult"></component>
      </div>
    </div>
  </div>
</template>

<script>
  import Upload1 from '../common/Upload1'
  import Upload2 from '../common/Upload2'
  import showPic from '../common/showPicture'
  import hisPad5 from './views/hisPad5'
  import hPad5Album from './views/hPad5Album'

  export default {
    name: 'History',
    data () {
      return {
        historyView:'Upload2',
        pad:'hPad1'
      }
    },
    methods:{
      changeHstView(data,pad){
        this.historyView= data;
        this.pad = pad;
      },
      handleResult(data){
        this.historyView= data;
      }
    },
    components:{
      Upload1,Upload2,showPic,hisPad5,hPad5Album
    },
    mounted(){
      this.$emit('changeFlag',true);
      this.$bus.$emit('changePad','History');
    }
  }
</script>


<style scoped>
  .history{
    display: flex;
  }
</style>
<style>
  .main{
    width:100%;
  }
  .small-menu{
    min-width:107px;
    height:100vh;
  }
  .title-name{
    /*控制菜单栏的标题*/
    font-size: 14px;
    color: #CACACA ;
    height:99px;
    display: flex;
    align-items:center;
    justify-content:center;
  }
  .history .ivu-layout-sider-children{
    /*控制小菜单栏背景颜色*/
    background: white;
    width:107px;
  }
  .history .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title {
    /*控制菜单栏里的文字不换行*/
    padding:14px 16px;
  }
  .ivu-menu-item{
    /*控制菜单栏大小*/
    height:72px;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu)::after {
    /*去掉选择条*/
    width:0;
  }
  .ivu-menu-vertical.ivu-menu-light::after {
    /*去掉选择条*/
    width:0;
  }
</style>




