<template>
  <div class="success2">
    <div class="layout">
      <!--<TopHead pad="msg" @changeView="changeView" v-if="flag"></TopHead>-->
      <div class="card">
        <Breadcrumb :style="{margin: '12px 0'}">
          <BreadcrumbItem>首页</BreadcrumbItem>
          <BreadcrumbItem>{{title1}}</BreadcrumbItem>
          <BreadcrumbItem>{{title2}}</BreadcrumbItem>
          Success2 - {{pad}}
        </Breadcrumb>
        <h1>{{title2}}</h1><br>
        <span>{{msg}}</span>
      </div>

      <div class="card">
        <div class="right">
          <div class="animate">
            <div class="tab-right">
              <transition name="tab">right
              </transition>
            </div>
            <span @click="viewPic">查看上传的图片</span>
            <Button @click="changeView"  type="primary" >下一步</Button>
          </div>
          <span class="grey">请等待审核，审核通过后将会展示</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'success2',
    data () {
      return {
        title1:'',
        title2:'',
        msg:'',
        flag:false,
      }
    },
    props:['pad'],
    methods:{
      changeView:function(){
        switch(this.pad){
          case 'pPad3':
            this.$emit('changePrepView','newAlbum','pPad4');
            break;
          case 'fPad3':
            this.$emit('changeFclView','Upload1','fPad4');
            break;
          case 'fPad4':
            this.$emit('changeFclView','uploadPicture','fPad5');
            break;
          case 'fPad6':
            this.$router.push({name:'History'});
            break;
          case 'hPad2':
            this.$emit('changeHstView','Upload2','hPad3');
            break;
          case 'hPad1':
            this.$emit('changeHstView','Upload1','hPad2');
            break;
          case 'hPad3':
            this.$emit('changeHstView','Upload1','hPad4');
            break;
          case 'hPad4':
            this.$emit('changeHstView','hisPad5','hPad5');
            break;
        }
      },
      viewPic(){
        this.$emit('changeFclView','showPic',this.pad);
        this.$emit('changePrepView','showPic',this.pad);
        this.$emit('changeHstView','showPic',this.pad);
      }
    },
    mounted: function(){
      switch(this.pad){
        default:
          this.title1='防灾设施';
          this.title2='接收传播设施';
          this.msg='大喇叭或显示屏等固定接收传播设施的照片';
          break;
        case 'fPad4':
          this.title1='防灾设施';
          this.title2='避灾场所';
          this.msg='避灾场所、警示牌和避灾路线图实景照片';
          break;
        case 'pPad3':
          this.title1='防灾准备';
          this.title2='应急认证';
          this.msg='扫描件已上传';
          break;
        case 'fPad6':
          this.title1='防灾设施';
          this.title2='科普宣传';
          this.msg='提交成功';
          break;
        case 'hPad1':
          this.title1='历史实践';
          this.title2='信息接收';
          this.msg='上传成功';
          break;
        case 'hPad2':
          this.title1='历史实践';
          this.title2='组织部署';
          this.msg='上传成功';
          break;
        case 'hPad3':
          this.title1='历史实践';
          this.title2='信息传播';
          this.msg='提交成功';
          break;
        case 'hPad4':
          this.title1='历史实践';
          this.title2='现场防御';
          this.msg='上传成功';
          break;
      }
    }
  }
</script>


<style scoped>
  .success2{
    min-height: 100%;
    border:0;
    margin:0 3px 0 3px;
  }
  .card{
    background: white;
    padding:40px;
    margin:10px;
  }
  .animate{
    width:100%;
    height:200px;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
  }
  .animate .tab-right{
    width:190px;
    height:190px;
    width: 100%;
    height: 179px;
    line-height: 20px;
    opacity: 0.7;
    border-radius: 5px 5px 5px 5px;
    margin-bottom:80px;
  }

  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .grey{
    color: #C1C1C1 ;
    font-size: 14px;
    font-family: MicrosoftYaHei;
  }
</style>
<style>
  .success2 .ivu-breadcrumb > span:last-child {
    color:#C1C1C1;
  }
  .success2 .ivu-menu-light.ivu-menu-horizontal .ivu-menu-item{
    border:0;
  }
  .success2 .ivu-menu-horizontal.ivu-menu-light::after {
    height:0;
  }
  .success2 .ivu-menu .ivu-icon{
    font-size: 20px;
    opacity:0.5;
  }
  .success2 .ivu-layout-header{
    padding:0 5px;
  }
  .success2 .ivu-btn{
    width:196px;
    height:50px;
  }
  .success2 .tab-enter {
    margin-right:0
  }

  .success2 .tab-enter-active {
    transition: all 1s;
  }

  .success2 .tab-enter-to {
    margin:auto;
  }
</style>



