<template>
  <div class="pPad2 pad">

    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>历史实践</BreadcrumbItem>
        <BreadcrumbItem>灾情调查</BreadcrumbItem>
        <BreadcrumbItem>提交成功</BreadcrumbItem>
      </Breadcrumb>
      <h1>提交成功</h1><br>{{pad}}
      <span>三种照片均已上传</span>
    </div>

    <div class="card center">
      <div class="albums">
        <div v-for="(item,index) in fAlbum5.items" v-if="item.status" :key="index" >
          <div class="album" @click="showPic(index)"></div>
          <p>{{item.title}}</p>
        </div>
      </div>
        <Button @click="changeView"  type="primary" :style="{width:'30%'}">完成</Button>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'pPad2',
    data () {
      return {
        fAlbum5: {
          items:[
            {
              id: 1,
              title:'现场调查照片',
              src:'',
              status: 1
            },
            {
              id: 2,
              title:'灾情实景照片',
              src:'',
              status: 1
            },
            {
              id: 3,
              title:'灾情上报记录截屏',
              src:'',
              status: 1
            },
            
          ]
        },
        title1:'',
        title2:'',
        msg:'',
      }
    },
    props:['pad'],
    methods:{
      showPic(index){
        this.$emit('result','showPic',index);
      },
      changeView(){
        this.$emit('changeFclView','Upload2','fPad6');
      }
    },
    mounted(){
      switch(this.pad){
        case 'fPad5':
          this.title1='防灾设施';
          this.title2='防雷设施';
          this.msg='两种照片均已上传';
          break;
      }
    }
  }
</script>


<style scoped>
  hr{
    background-color:rgb(220, 222, 226) ;
    height:2px;
    border:0;
  }
  .center{
    text-align: center;
  }
  .albums{
    display: flex;
    justify-content:space-around;
    text-align: center;
    margin:20px;
  }
  .album{
    width:260px;
    height:156px;
    background-color:#C4CDFF ;
    margin:auto;
  }
</style>
<style>

</style>



