<template>
  <div class="pPad2 pad">

    <div class="card" @click="changePad2('addAlbum')">
      <h2>新建照片组</h2>newAlbum - {{pad}}
    </div>
    <div class="card albums">
      <Row>
        <Col span="8" v-for="(item,index) in album1.items" v-if="item.status" :key="index" @click.native="changePad2('showPic')">
          <div class="album"></div>
          <p>{{item.title}}</p>
        </Col>
      </Row>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'pPad2',
    data () {
      return {
        album1: {
          items:[
            {
              id: 1,
              title:'由雷锋同志组织的防雷行动',
              src:'',
              status: 1
            },
            {
              id: 2,
              title:'由雷锋同志组织的防雷行动',
              src:'',
              status: 1
            },
            {
              id: 3,
              title:'由雷锋同志组织的防雷行动',
              src:'',
              status: 1
            },
            {
              id: 3,
              title:'由雷锋同志组织的防雷行动',
              src:'',
              status: 1
            },{
              id: 3,
              title:'由雷锋同志组织的防雷行动',
              src:'',
              status: 1
            },{
              id: 3,
              title:'由雷锋同志组织的防雷行动',
              src:'',
              status: 1
            },
          ]
        }
      }
    },
    props:['pad'],
    methods:{
      changePad2:function(data){
        this.$emit('result',data);
      },
    }
  }
</script>


<style scoped>
  hr{
    background-color:rgb(220, 222, 226) ;
    height:2px;
    border:0;
  }
  .albums{
    display: flex;
    justify-content:space-around;
    flex-flow:wrap;
    text-align: center;
  }
  .album{
    width:260px;
    height:156px;
    background-color:#C4CDFF ;
    margin:auto;
  }
</style>
<style>

</style>



