<template>
  <div class="success1">
      <!--<TopHead pad="msg" @changeView="changeView" v-if="flag"></TopHead>-->
      <div class="card">
        <Breadcrumb :style="{margin: '12px 0'}">
          <BreadcrumbItem>首页</BreadcrumbItem>
          <BreadcrumbItem>{{title1}}</BreadcrumbItem>
          <BreadcrumbItem>{{title2}}</BreadcrumbItem>
          <BreadcrumbItem>{{title3}}</BreadcrumbItem>
          Success1 - {{pad}}
        </Breadcrumb>
        <h1>{{title3}}</h1><br>
        <span>{{msg}}</span>
      </div>

      <div class="card">
        <div class="right">
          <div class="animate">
            <div class="tab-right">
              <transition name="tab">right
              </transition>
            </div>
            <Button @click="changeView"  type="primary" >下一步</Button>
          </div>
          <span class="grey">请等待审核，审核通过后将会展示</span>
        </div>
      </div>
  </div>
</template>

<script>
  export default {
    name: 'Success1',
    data () {
      return {
        title1:'',
        title2:'',
        title3:'',
        msg:'',
        flag:false,
      }
    },
    props:['pad'],
    methods:{
      changeView:function(){
        switch(this.pad){
          case 'msg':
            this.$router.push({name:'System'});
            break;
          case 'Pad1':
            this.$emit('changeSystemView','Pad2');
            break;
          case 'Pad3':
            this.$emit('changeSystemView','Pad4');
            break;
          case 'Pad4':
            this.$emit('changeSystemView','Pad5');
            break;
          case 'pPad2':
            this.$emit('changePrepView','uploadPicture','pPad3');
            break;
          case 'pPad3':
            this.$emit('changePrepView','newAlbum','pPad4');
            break;
          case 'pPad4':
            this.$emit('changePrepView','newAlbum','pPad5');
            break;
          case 'pPad5':
            this.$router.push({name:'Facility'});
            break;
          case 'fPad1':
            this.$emit('changeFclView','newAlbum','fPad2');
            break;
          case 'fPad2':
            this.$emit('changeFclView','Upload1','fPad3');
            break;
        }

      }
    },
    mounted: function(){
      switch(this.pad){
        case 'msg':
          this.title1='基本概况';
          this.title2='填写表单';
          this.title3='提交完成';
          this.msg='';
          this.flag=true;
          break;
        case 'Pad1':
          this.title1='制度机制';
          this.title2='组织架构';
          this.title3='组织架构和责任人基本信息';
          this.msg='';
          this.flag=true;
          break;
        case 'Pad3':
          this.title1='制度机制';
          this.title2='工作制度';
          this.title3='日常工作制度';
          this.msg='';
          break;
        case 'Pad4':
          this.title1='制度机制';
          this.title2='应急处置表';
          this.title3='应急处置表';
          this.msg='气象灾害预警信号为先导的分灾种气象灾害';
          break;
        case 'pPad2':
          this.title1='防灾准备';
          this.title2='应急演练';
          this.title3='新建照片组';
          this.msg='';
          break;
        case 'pPad3':
          this.title1='防灾准备';
          this.title2='应急认证';
          this.title3='提交成功';
          this.msg='扫描件已上传';
          break;
        case 'pPad4':
          this.title1='防灾准备';
          this.title2='科普培训';
          this.title3='新建照片组成功';
          this.msg='';
          break;
        case 'pPad5':
          this.title1='防灾准备';
          this.title2='科普活动';
          this.title3='新建照片组成功';
          this.msg='';
          break;
        case 'fPad1':
          this.title1='防灾设施';
          this.title2='气象服务站';
          this.title3='新建照片组成功';
          this.msg='';
          break;
        case 'fPad2':
          this.title1='防灾设施';
          this.title2='监测设施';
          this.title3='新建照片组成功';
          this.msg='';
          break;
        case 'fPad5':
          this.title1='防灾设施';
          this.title2='监测设施';
          this.title3='新建照片组成功';
          this.msg='';
          break;
      }
    }
  }
</script>


<style scoped>
  .success1{
    min-height: 100%;
    border:0;
    margin:0 3px 0 3px;
  }
  .card{
    background: white;
    padding:40px;
    margin:10px;
  }
  .animate{
    width:100%;
    height:200px;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
  }
  .animate .tab-right{
    width:190px;
    height:190px;
    width: 100%;
    height: 179px;
    line-height: 20px;
    opacity: 0.7;
    border-radius: 5px 5px 5px 5px;
    margin-bottom:80px;
  }

  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .grey{
    color: #C1C1C1 ;
    font-size: 14px;
    font-family: MicrosoftYaHei;
  }
</style>
<style>
  .success1 .ivu-breadcrumb > span:last-child {
    color:#C1C1C1;
  }
  .success1 .ivu-menu-light.ivu-menu-horizontal .ivu-menu-item{
    border:0;
  }
  .success1 .ivu-menu-horizontal.ivu-menu-light::after {
    height:0;
  }
  .success1 .ivu-menu .ivu-icon{
    font-size: 20px;
    opacity:0.5;
  }
  .success1 .ivu-layout-header{
    padding:0 5px;
  }
  .success1 .ivu-btn{
    width:196px;
    height:50px;
  }
  .success1 .tab-enter {
    margin-right:0
  }

  .success1 .tab-enter-active {
    transition: all 1s;
  }

  .success1 .tab-enter-to {
    margin:auto;
  }
</style>



