<template>
  <div class="pad6 pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>制度机制</BreadcrumbItem>
        <BreadcrumbItem>填写表单</BreadcrumbItem>
        <BreadcrumbItem>辖区内重点单位及联系人基本信息一览表</BreadcrumbItem>
      </Breadcrumb>
      <h1>辖区内重点单位及联系人基本信息一览表</h1><br>
    </div>

    <div class="card" v-for="(item,index) in form.items" v-if="item.status">
      <Form :model="item"  label-position="left" :label-width="70" inline :style="{textAlign:'center'}">
        <!--表头-->
        <div class="form-header">
          <div class="right">{{index}}
            <span v-show="!item.showIcon">
              <span @click="deletePerson">
              <Icon type="ios-trash-outline" />
              删除</span>&nbsp;&nbsp;
            </span>
            <span v-show="item.showIcon">
              <span @click="addPerson(index)">
              <Icon type="ios-add-circle-outline" />
              添加接收人</span>
            </span>
          </div>
        </div>

        <!--表体-->
        <div class="forms">
          <div class="form">
            <hr>
            <div class="form-card" inline>
              <div class="center">
                <FormItem label="单位名称：">
                  <Input v-model="item.company"></Input>
                </FormItem>
                <FormItem label="类型：">
                  <Input v-model="item.type"></Input>
                </FormItem>
                <FormItem label="联系人：">
                  <Input v-model="item.name"></Input>
                </FormItem>
                <FormItem label="联系电话：">
                  <Input v-model="item.phone"></Input>
                </FormItem>
              </div>
              <div class="center">
                <FormItem label="联系地址：">
                  <Input v-model="item.address" :style="{width:'355px'}"></Input>
                </FormItem>
                <FormItem label="单位地址：">
                  <Input v-model="item.companyAddress" :style="{width:'355px'}"></Input>
                </FormItem>
              </div>
            </div>
          </div>
        </div>
        <Button type="primary" :style="{width:'18%'}" @click.native="handleSubmit" v-show="item.showButton">提交</Button>
      </Form>
    </div>
    <div class="showPsn card">
      <Table border :columns="columns" :data="form6.items">
        <template slot-scope="{ index}" slot="name">
          <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{ row }" slot="name">
          <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{ row, index }" slot="action">
          <Button type="primary" size="small" style="margin-right: 5px" @click="change(index)">修改</Button>
          <Button type="error" size="small" @click="remove(index)">删除</Button>
        </template>
      </Table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'pad6',
    data () {
      return {
        index:1,
        submitOk:false,
        form: {
          items:[
            {
              company: '',
              type: '',
              name: '',
              phone: '',
              address: '',
              companyAddress: '',
              status: 1,
              showButton:true,
              showIcon:true,
            },
          ]
        },//用户输入数据
        form6: {
          items:[
            {
              company: '浙江广晟',
              type: '事业单位',
              name: '李大二',
              phone: '18765431234',
              address: '吐鲁番',
              companyAddress: '中国撒哈拉大沙漠',
              status: 1,
              showButton:true,
              showIcon:true,
            },
          ]
        },//服务器得到的数据
        columns: [
          {
            title: '序号',
            type: 'index',
            width: 70,
          },
          {
            title: '单位名称',
            key: 'company',
            width: 90,
          },
          {
            title: '类型',
            key: 'type',
            width: 120,
          },
          {
            title: '联系人',
            key: 'name',
            width: 100,
          },
          {
            title: '联系电话',
            key: 'phone',
            width: 120,
          },
          {
            title: '联系地址',
            key: 'address',
            width: 150,
          },
          {
            title: '单位地址',
            key: 'companyAddress',
            width:'250'
          },
          {
            title: '编辑',
            slot: 'action',
            align: 'center'
          }
        ],

      }
    },
    methods:{
      deletePerson:function(){
        this.form.items.pop();
        let length=this.form.items.length;
        this.form.items[length-1].showButton = true;
      },
      addPerson:function(){
        let length=this.form.items.length;
        console.log(length);
        this.form.items.push({
          index:length,
          company: '',
          type: '',
          name: '',
          phone: '',
          address: '',
          companyAddress: '',
          status: 1,
          showButton:true,
          showIcon: false,
        });
        this.form.items.forEach(item => { item.showButton= false;})
        this.form.items[length].showButton = true;
      },
      handleSubmit:function(){
        this.$Modal.success({
          title: '您新增的信息已提交',
          content:'待系统录入后即会显示在此页，可能会需要几分钟，请耐心等待',
          okText: '下一步',
          onOk: () => {
            this.$emit('changeSystemView','Pad7');
          },
        });
      },
      change(index){
        // this.$Modal.info({
        //   title: 'User Info',
        //   content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
        // })
      },
      remove(index){
        this.form6.items.splice(index, 1);
      },
      ok(){}
    },
    mounted(){
      this.$bus.$emit('changeSmallPad','1-2');
    }
  }
</script>


<style scoped>
  .form-header{
    height:20px;
  }
  .right{
    float:right;
  }

  .center{
    display:flex;
    justify-content: center;
    width:900px;
    margin:auto;
  }
  hr{
    background-color:rgb(220, 222, 226) ;
    height:2px;
    border:0;
  }
  .form-card{
    padding:10px 50px;
    margin:auto;
  }
</style>
<style>
  .pad6 .ivu-form-item{
    margin:8px;
  }
  .pad6 .ivu-form-item-label{
    padding: 10px 2px 10px 8px;
  }
</style>



