<template>
  <div id="Message">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>基本情况</BreadcrumbItem>
        <BreadcrumbItem>填写表单</BreadcrumbItem> Message
      </Breadcrumb>
      <h1>填写基本概况</h1><br>
      <span class="grey">填写地理位置、面积、人口、主要气象灾害等基本概况</span>
    </div>
    <div class="card">
      <div class="form">
        <Form :model="msgForm" :label-width="200">
          <FormItem label="地址:">
            <Row :gutter="16">
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.city" placeholder="市">
                  <template v-for="(item,index) in addr">
                    <Option v-bind:value="index" @click.native="getCity">{{item.name}}</Option>
                  </template>
                </Select>
              </Col>
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.county" placeholder="区/县" not-found-text="请先选择'市'" >
                  <template v-for="(item,index) in showCounties">
                    <Option v-bind:value="index" @click.native="getCounty">{{item.name}}</Option>
                  </template>
                </Select>
              </Col>
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.town"  placeholder="街道/镇" not-found-text="请先选择'区/县'">
                  <template v-for="(item,index) in showTowns">
                    <Option v-bind:value="index" @click.native="getTown">{{item.name}}</Option>
                  </template>
                </Select>
              </Col>
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.village"  placeholder="社区/村" not-found-text="请先选择'街道/镇'">
                  <template v-for="(item,index) in showVillages">
                    <Option v-bind:value="index" @click.native="getVillage">{{item}}</Option>
                  </template>
                </Select>
              </Col>
            </Row>
          </FormItem>
          <div class="short">
            <FormItem label="面积：">
              <Input v-model="msgForm.input4"></Input>m²
            </FormItem>
            <FormItem label="人口：">
              <Input v-model="msgForm.input5"></Input>人
            </FormItem>
          </div>
          <FormItem label="主要气象灾害：">
            <Select v-model="msgForm.input6"  placeholder="洪涝">
              <template v-for="item in disaster">
                <Option v-bind:value="item">{{item}}</Option>
              </template>
            </Select>
          </FormItem>
          <FormItem>
            <Button type="primary" :style="{width:'50%'}" @click.native="handleSubmit">提交</Button>
          </FormItem>
        </Form>
      </div>
      <span class="grey">请保证填写的信息尽量精准</span>
    </div>

  </div>
</template>
<script>
  export default {
    name: 'Message',
    data(){
      return {
        addr:[],
        showCounties:[],
        showTowns:[],
        showVillages:[],
        disaster:['洪涝','暴雨','干旱'],
        msgForm: {
          city:'',
          county:'',
          town:'',
          village:'',
          input4: '',
          input5: '',
          input6: ''
        }
      }
    },
    methods:{
      handleSubmit:function(){
        this.$emit('changeView',"Success1");
      },
      getCity(){
        this.showCounties=this.addr[this.msgForm.city].counties;
      },
      getCounty(){
        this.showTowns=this.showCounties[this.msgForm.county].towns;
      },
      getTown(){
        this.showVillages=this.showTowns[this.msgForm.town].villages;
      },
      getVillage(){

      },

    },
    created(){
      fetch('../../static/addr.json')
        .then(response=>response.json())
        .then(json=>{
          this.addr=json;
        })
    },
    mounted(){
      this.$bus.$emit('changePad','Index');
    }
  }
</script>

<style scoped>
  .form{
    padding:50px;
    width:800px;
    /*控制表单大小*/
    height:57vh;
    margin:auto;
  }

</style>



