<template>
  <div class="hisPad5">

    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>历史实践</BreadcrumbItem>
        <BreadcrumbItem>灾情调查</BreadcrumbItem>
        hisPad5 -{{pad}}
      </Breadcrumb>
      <h1>灾情调查</h1><br>
      <span>三种照片都要上传</span>
    </div>

    <div class="card center">
      <div class="upload">
        <div class="add">
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button icon="ios-cloud-upload-outline">Upload files</Button>
          </Upload>
          <p>1.现场调查的照片</p>
        </div>
        <div class="add">
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button icon="ios-cloud-upload-outline">Upload files</Button>
          </Upload>
          <p>2.灾情实景照片</p>
        </div>
        <div class="add">
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button icon="ios-cloud-upload-outline">Upload files</Button>
          </Upload>
          <p>3.灾情上报记录截屏（包括但不限于信息上报平台、通过QQ群、微信群、钉钉群上传信息的截屏）</p>
        </div>
      </div>
      <Button type="primary" :style="{width:'40%'}" @click="handleSubmit">提交</Button>
    </div>

  </div>
</template>

<script>
  export default {
    name: "hisPad5",
    data(){
      return {

      }
    },
    props:['pad'],
    methods: {
      handleSubmit: function () {
        this.$emit('result', 'hPad5Album');
      },
    },
  }
</script>

<style scoped>
  .card{
    background: white;
    padding:40px;
    margin:10px;
  }
  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .center{
    text-align: center;
  }
  .upload{
    display: flex;
  }
  .add{
    width:340px;
    height:300px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }
</style>



