<template>
  <div id="sideMenu">
      <div id="menu">
        <Sider :style="{position: 'fixed', height: '100vh', left: 0, overflow: 'auto',color:'white',width:'210px',maxWidth:'210px'}">
          <Menu :active-name="currentView" theme="dark" width="auto" :open-names="['1-1']">
              <div class="title" >
                <div class="icon"><Icon type="ios-navigate"></Icon></div>
                <div class="title-name"><span><h2>标准化村/社区</h2></span></div>
              </div>
            <MenuItem name="Index" @click.native="handleChangeView('Index')">
              <Icon type="ios-search"></Icon>
              <span>基本概况</span>
            </MenuItem>
            <MenuItem name="System" @click.native="handleChangeView('System')">
              <Icon type="ios-settings"></Icon>
              <span>制度机制</span>
            </MenuItem>
            <MenuItem name="Prepare" @click.native="handleChangeView('Prepare')">
              <Icon type="ios-settings"></Icon>
              <span>防灾准备</span>
            </MenuItem>
            <MenuItem name="Facility" @click.native="handleChangeView('Facility')">
              <Icon type="ios-settings"></Icon>
              <span>防灾设施</span>
            </MenuItem>
            <MenuItem name="History" @click.native="handleChangeView('History')">
              <Icon type="ios-settings"></Icon>
              <span>历史实践</span>
            </MenuItem>
            {{currentView}}
          </Menu>
        </Sider>
      </div>
  </div>
</template>
<script>
  export default {
    name: 'SideMenu',
    data() {
      return {
        currentView: 'Index',
        // activePad:'1-1'
      }
    },
    methods:{
      handleChangeView:function(v){
            this.currentView=v;
            console.log(v);
            // this.$emit('view',v);
        this.$router.push({name:v});
      }
    },
    mounted:function(){
      let _this=this;
      _this.$bus.$off("changePad");
      _this.$bus.$on('changePad',function(pad){
        _this.currentView = pad;
      })

    }
  }
</script>

<style scoped>

</style>
<style>
  #sideMenu[data-v-fcd3770a]{
    min-width:210px;
    text-align: center;
  }
  #menu .title{
    height:68px;
    display:flex;
    justify-content: center;
    align-items:center;
    padding:14px 24px;
    color:white;
  }
  .title .icon{
    margin:0 6px 0 0;
  }
  .title .title-name{
    text-align: center;
  }
  h2{
    font-size:18px;
  }
  #menu .ivu-layout-sider-children{
    background-color: #001529;
  }
  #menu .ivu-menu{
    background-color: #001529;
  }
  #menu .ivu-menu-dark.ivu-menu-vertical .ivu-menu-item, .ivu-menu-dark.ivu-menu-vertical .ivu-menu-submenu-title{
    color:white;
  }
  #menu .ivu-menu-dark.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu), .ivu-menu-dark.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):hover, .ivu-menu-dark.ivu-menu-vertical .ivu-menu-submenu-title-active:not(.ivu-menu-submenu), .ivu-menu-dark.ivu-menu-vertical .ivu-menu-submenu-title-active:not(.ivu-menu-submenu):hover{
    background-color:  #1890FF ;
  }
  #menu .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title{
    padding:18px 24px;
  }
</style>



