<template>
  <div class="facility grey">
    <div class="small-menu">
      <Sider :style="{ position: 'fixed',height: '100vh', left: '210px',color:'white',minWidth:'107px',maxWidth:'107px',flexBasis: '107px'}">
        <div class="title">
          <div class="title-name">防灾设施</div>
        </div>
        <Menu :active-name=pad theme="light" width="107px" >
          <MenuItem name="fPad1" @click.native="changeFclView('newAlbum','fPad1')">
            <span>气象服务站</span>
          </MenuItem>
          <MenuItem name="fPad2" @click.native="changeFclView('newAlbum','fPad2')">
            <span>监测设施</span>
          </MenuItem>
          <MenuItem name="fPad3" @click.native="changeFclView('Upload1','fPad3')">
            <span>接收传播设施</span>
          </MenuItem>
          <MenuItem name="fPad4" @click.native="changeFclView('Upload1','fPad4')">
            <span>避灾场所</span>
          </MenuItem>
          <MenuItem name="fPad5" @click.native="changeFclView('uploadPicture','fPad5')">
            <span>防雷设施</span>
          </MenuItem>
          <MenuItem name="fPad6" @click.native="changeFclView('Upload2','fPad6')">
            <span>科普宣传</span>
          </MenuItem>
        </Menu>
      </Sider>
    </div>
    <div class="main">
      <top-head :pad="pad" :currentView="facilityView" @changeView="changeFclView"></top-head>
      <div class="facilityViews">
        <component :is="facilityView" @changeFclView="changeFclView" :pad="pad" @result="handleResult"></component>
      </div>
    </div>
  </div>
</template>

<script>
  import newAlbum from '../common/newAlbum'
  import uploadPicture from '../common/uploadPicture'
  import Upload1 from '../common/Upload1'
  import Upload2 from '../common/Upload2'
  import addAlbum from '../common/addAlbum'
  import showPic from '../common/showPicture'
  import fPad5Album from './views/fPad5Album'

  export default {
    name: 'facility',
    data () {
      return {
        facilityView:'newAlbum',
        pad:'fPad1'
      }
    },
    methods:{
      changeFclView(data,pad){
        this.facilityView= data;
        this.pad = pad;
      },
      handleResult(data){
        this.facilityView= data;
      }
    },
    components:{
      newAlbum,uploadPicture,Upload1,Upload2,addAlbum,showPic,fPad5Album
    },
    mounted(){
      this.$emit('changeFlag',true);
      this.$bus.$emit('changePad','Facility');

    }
  }
</script>


<style scoped>
  .facility{
    display: flex;
  }
</style>
<style>
  .main{
    width:100%;
  }
  .small-menu{
    min-width:107px;
    height:100vh;
  }
  .title-name{
    /*控制菜单栏的标题*/
    font-size: 14px;
    color: #CACACA ;
    height:99px;
    display: flex;
    align-items:center;
    justify-content:center;
  }
  .facility .ivu-layout-sider-children{
    /*控制小菜单栏背景颜色*/
    background: white;
    width:107px;
  }
  .facility .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title {
    /*控制菜单栏里的文字不换行*/
    padding:14px 16px;
  }
  .ivu-menu-item{
    /*控制菜单栏大小*/
    height:72px;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu)::after {
    /*去掉选择条*/
    width:0;
  }
  .ivu-menu-vertical.ivu-menu-light::after {
    /*去掉选择条*/
    width:0;
  }
</style>




