<template>
  <div class="addAlbum pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem>首页</BreadcrumbItem>
        <BreadcrumbItem>防灾准备</BreadcrumbItem>
        <BreadcrumbItem>{{padName}}</BreadcrumbItem>
        <BreadcrumbItem>新建照片组</BreadcrumbItem>
      </Breadcrumb>
      <h1>{{padName}}照片组</h1><br>addAlbum-{{pad}}
    </div>
    <div class="card">
      <div class="form">
        <Form :model="pform2" label-position="right" :label-width="100" >
          <FormItem label="演练地点：">
            <Select v-model="pform2.input1" placeholder="市">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <Select v-model="pform2.input2" placeholder="区">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <Select v-model="pform2.input3" placeholder="县/村">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="具体地点：">
            <Input v-model="pform2.input4"></Input>
          </FormItem>
          <FormItem label="演练时间：">
            <Input v-model="pform2.input5" ></Input>
          </FormItem>
          <FormItem label="组织方：">
            <Input :rows="6" v-model="pform2.input6"></Input>
          </FormItem>
          <FormItem label="参与人：">
            <Input :rows="6" v-model="pform2.input7"></Input>
          </FormItem>
          <FormItem label="基本情况介绍：">
            <Input type="textarea" :rows="5" v-model="pform2.input8"></Input>
          </FormItem>
          <FormItem label="添加照片：">
            <div class="demo-upload-list" v-for="item in uploadList">
              <template v-if="item.status === 'finished'">
                <img :src="item.url">
                <div class="demo-upload-list-cover">
                  <Icon type="ios-eye-outline" @click.native="handleView(item.name)"></Icon>
                  <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                </div>
              </template>
              <template v-else>
                <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
              </template>
            </div>
            <Upload
              ref="upload"
              :show-upload-list="false"
              :default-file-list="defaultList"
              :on-success="handleSuccess"
              :format="['jpg','jpeg','png']"
              :max-size="2048"
              :on-format-error="handleFormatError"
              :on-exceeded-size="handleMaxSize"
              :before-upload="handleBeforeUpload"
              multiple
              type="drag"
              action="//jsonplaceholder.typicode.com/posts/"
              style="display: inline-block;width:58px;">
              <div style="width: 58px;height:58px;line-height: 58px;">
                <Icon type="ios-camera" size="20"></Icon>
              </div>
            </Upload>
            <Modal title="View Image" v-model="visible">
              <img :src="'https://o5wwk8baw.qnssl.com/' + imgName + '/large'" v-if="visible" style="width: 100%">
            </Modal>
          </FormItem>
          <FormItem>
            <Button type="primary" :style="{width:'70%'}" @click="changeView">提交</Button>
          </FormItem>
        </Form>
        <div>
        </div>
      </div>
      <span class="grey">最多添加3张照片，最多15张</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'addAlbum',
    data () {
      return {
        pform2: {
          input1: '',
          input2: '',
          input3: '',
          input4: '',
          input5: '',
          input6: '',
          input7: '',
          input8: '',
        },
        defaultList: [
          {
            'name': 'a42bdcc1178e62b4694c830f028db5c0',
            'url': 'https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar'
          },
          {
            'name': 'bc7521e033abdd1e92222d733590f104',
            'url': 'https://o5wwk8baw.qnssl.com/bc7521e033abdd1e92222d733590f104/avatar'
          }
        ],
        imgName: '',
        visible: false,
        uploadList: [],
        padName:''
      }
    },
    props:['pad'],
    methods:{
      changeView:function(){
        this.$emit('result','Success1');
      },
      handleView (name) {
        this.imgName = name;
        this.visible = true;
      },
      handleRemove (file) {
        const fileList = this.$refs.upload.fileList;
        this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
      },
      handleSuccess (res, file) {
        file.url = 'https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar';
        file.name = '7eb99afb9d5f317c912f08b5212fd69a';
      },
      handleFormatError (file) {
        this.$Notice.warning({
          title: 'The file format is incorrect',
          desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
        });
      },
      handleMaxSize (file) {
        this.$Notice.warning({
          title: 'Exceeding file size limit',
          desc: 'File  ' + file.name + ' is too large, no more than 2M.'
        });
      },
      handleBeforeUpload () {
        const check = this.uploadList.length < 5;
        if (!check) {
          this.$Notice.warning({
            title: 'Up to five pictures can be uploaded.'
          });
        }
        return check;
      }
    },
    mounted: function(){
      switch(this.pad){
        case 'pPad4':
          this.padName='科普培训';
          break;
        case 'pPad5':
          this.padName='科普活动';
          break;
        case 'fPad1':
          this.padName='气象服务站';
          break;
        case 'fPad2':
          this.padName='监测设施';
          break;
        default:
          this.padName='应急演练';
          break;
      }
    }
  }
</script>


<style scoped>
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:30px;
  }
  .addAlbum .ivu-form{
    width:500px;
  }
  .addAlbum .ivu-select{
    width:30%;
  }
  .grey{
    color: #C1C1C1 ;
    font-size: 14px;
    font-family: MicrosoftYaHei;
  }
</style>



