<template>
  <div class="prepare grey">
    <div class="small-menu">
      <Sider :style="{ position: 'fixed',height: '100vh', left: '210px',color:'white',minWidth:'107px',maxWidth:'107px',flexBasis: '107px'}">
        <div class="title">
          <div class="title-name">防灾准备</div>
        </div>
        <Menu :active-name="pad" theme="light" width="107px" >
          <MenuItem name="pPad1" @click.native="changePrepView('Pad1','pPad1')">
            <span>风险地图</span>
          </MenuItem>
          <MenuItem name="pPad2" @click.native="changePrepView('newAlbum','pPad2')">
            <span>应急演练</span>
          </MenuItem>
          <MenuItem name="pPad3" @click.native="changePrepView('uploadPicture','pPad3')">
            <span>应急认证</span>
          </MenuItem>
          <MenuItem name="pPad4" @click.native="changePrepView('newAlbum','pPad4')">
            <span>科普培训</span>
          </MenuItem>
          <MenuItem name="pPad5" @click.native="changePrepView('newAlbum','pPad5')">
            <span>科普活动</span>
          </MenuItem>
        </Menu>
      </Sider>
    </div>
    <div class="main">
      <top-head :pad="pad" :currentView='prepareView' @changePrepView="changePrepView"></top-head>
      <div class="prepareViews">
        <component :is="prepareView" @changePrepView="changePrepView" :pad="pad" @result="handleResult"></component>
      </div>
    </div>
  </div>
</template>

<script>
  import Pad1 from './views/pPad1'
  import newAlbum from '../common/newAlbum'
  import uploadPicture from '../common/uploadPicture'
  import addAlbum from '../common/addAlbum'
  import showPic from '../common/showPicture'
  export default {
    name: 'Prepare',
    data () {
      return {
        prepareView:'Pad1',
        pad:'pPad1',
      }
    },
    methods:{
      changePrepView(data,pad){
        this.prepareView = data;
        console.log(data);
        this.pad = pad;
      },
      handleResult(data){
        this.prepareView = data;
      }
    },
    components:{
       Pad1,newAlbum,uploadPicture,addAlbum,showPic
    },
    mounted(){
      this.$emit('changeFlag',true);
      this.$bus.$emit('changePad','Prepare');
    }
  }
</script>


<style scoped>
  .prepare{
    display: flex;
  }
</style>
<style>
  .main{
    width:100%;
  }
  .small-menu{
    min-width:107px;
    height:100vh;
  }
  .title-name{
    /*控制菜单栏的标题*/
    font-size: 14px;
    color: #CACACA ;
    height:99px;
    display: flex;
    align-items:center;
    justify-content:center;
  }
  .prepare .ivu-layout-sider-children{
    /*控制小菜单栏背景颜色*/
    background: white;
    width:107px;
  }
  .prepare .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title {
    /*控制菜单栏里的文字不换行*/
    padding:14px 16px;
  }
  .ivu-menu-item{
    /*控制菜单栏大小*/
    height:72px;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu)::after {
    /*去掉选择条*/
    width:0;
  }
  .ivu-menu-vertical.ivu-menu-light::after {
    /*去掉选择条*/
    width:0;
  }

</style>
<link rel="stylesheet" type="text/css" href="./assets/index.css">



