<template>
  <div class="index grey">
    <top-head :pad='pad' @changeView="changeView"></top-head>
    <div id="view">

      <component :is="view" @changeView="changeView" :pad="pad"></component>
    </div>

    </div>
</template>
<script>
  import Message from './Message/Message'

  export default {
    name: 'Index',
    data(){
      return {
        view:'Message',
        pad:'msg'
      }
    },
    components:{
      Message,
    },
    methods:{
      changeView:function(data){
        this.view = data;
        console.log(this.view);
      },
    },
    created(){
      this.$emit('changeFlag',true);
    }
  }
</script>

<style scoped>
</style>
